/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1.1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --corpRed: hsl(4, 77%, 54%);
  --lightGrey: hsl(0, 0%, 83%);
  --midGrey: hsl(0, 0%, 41%);
  --darkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 9%);
  --white: hsl(0, 0%, 100%);
  --textGrey: hsl(0, 0%, 445);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
  --font-heading: "Roboto", Helvetica, Arial, sans-serif; /* heading font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--black);
}

.container-lg {
  width: 80%;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container-lg {
    width: 100%;
    position: relative;
  }
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container {
    width: 100%;
    position: relative;
    padding: 0 0.625rem 0 0.625rem;
  }
}

.row {
  padding: 0 0.625rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 75em) {
  .hide-for-desktop {
    display: none;
  }
}

/* Full Width
/* -------------------------------------*/
.fullWidth__blogs--wrapper {
  width: 60%;
  max-width: 75rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (max-width: 63.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: row;
  }
}
@media (max-width: 39.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: column;
  }
}
.fullWidth__rowReverse {
  flex-direction: row-reverse;
}
.fullWidth__section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  justify-content: flex-start;
  align-items: center;
}
.fullWidth__section--reverse {
  flex-direction: row-reverse;
}
@media (max-width: 39.9375em) {
  .fullWidth__section {
    display: block;
    height: 100%;
    position: relative;
    overflow: visible;
  }
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-heading);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  background-color: var(--white);
}
@media (max-width: 39.9375em) {
  .header {
    height: 5.625rem;
  }
}
@media (min-width: 75em) {
  .header .hamburger {
    visibility: hidden;
  }
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  top: 5.625rem;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
}
@media (min-width: 40em) {
  .header .overlay {
    top: 6.875rem;
  }
}
@media (max-width: 39.9375em) {
  .header .overlay {
    position: absolute;
  }
}
.header__logo {
  height: 5.1875rem;
  padding-top: 0.625rem;
  display: block;
}
@media (max-width: 39.9375em) {
  .header__logo img {
    width: 100%;
  }
}